.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card {
  width: 20rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin: 10px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.card-img-top {
  border-radius: 5px 5px 0 0;
  width: 100%;
}

.card-body {
  padding: 15px;
}

.card-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}

.card-text {
  font-size: 16px;
  margin-bottom: 15px;
}

.card-deck{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
}

.visible {
  display: block;
}

.hidden {
  display: none;
}

.card:hover {
  cursor: pointer;
}

button:hover{
  cursor: pointer;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align:center;
}

button{
  font-size: 20px;
  margin: 10px 10px 10px 10px;
  padding: 20px 20px 20px 20px;
  text-shadow: 2px 2px yellow;
  border-radius: 80%;
  background-color: limegreen;
}